@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Anton&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    user-select: none;
    font-family: "montserrat";
}

.topbar{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
    height: 80px;
    background-color: #151529;
    /* background-color: #030303; */
    /* background-color: white; */
    /* background-color: #2941ab; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.5); */
    /* border-bottom: 1px solid rgba(0,0,0,0.2); */
}

.innertopbar{
    /* width: 1800px; */
    width: 100%;
    height: 100%;
    /* background-color: black; */
    padding: 0px 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* background-color: aqua; */
}

.logopart{
    width: auto;
    height: 100%;
    /* background-color: black; */
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.logo{
    height: 40%;
    width: auto;
    margin-right: 3px;
}
.logopart span{
    font-size: 1.6em;
    font-weight: 400;
    font-family: "anton";
    color: white;
    /* color: #066bff; */
    /* letter-spacing: -1px; */
    /* padding-right: 10px; */
}

.github,.linkedin{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
    height: auto;
    margin: 0px 10px;
}
.linkedin{
    margin-right: 20px;
}

.menupart{
    width: auto;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: blue; */
}
.launch-button{
    width: auto;
    height: 50%;
    padding: 0px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-color: #066bff; */
    transition: background-color 0.1s ease-in-out;
    border-radius: 3px;
    cursor: pointer;
}
.launch-button:hover{
    background-color: #066bff;
}
.launch-button p{
    color: white;
    font-size: 1em;
    font-family: "montserrat";
    font-weight: 500;
}
.logintab,.launch-player{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0.9em;
    color: white;
    font-weight: 600;
}
.logintab{
    width: 100px;
    height: 100%;
}
.launch-player{
    width: 160px;
    height: 100%;
}
.launch-player img, .logintab img{
    width: 20px;
    height: auto;
}
.launch-player p, .logintab p{
    cursor: pointer;
}
.launch-player p:hover, .logintab p:hover{
    text-decoration: underline;
}



.login-top{
    /* position: fixed; */
    position: absolute;
    top: 0;
    width: 100%;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
    box-shadow: 0px 0px 5px 0px black;
}
.login-top.dark{
    background-color: #030303;
    box-shadow: 0px 0px 5px 0px rgba(255,255,255,0.2);
}
.login-top img{
    height: 50%;
    width: auto;
}
.login-top span{
    color: black;
    font-family: "anton";
    margin-left: 5px;
    font-size: 2.5em;
    padding-right: 10px;
}
.login-top.dark span{
    color: white;
}
.tm{
    font-family: "montserrat";
    font-size: 0.9em;
    color: black;
}


.profilepart{
    width: 40px;
    height: 100%;
    /* background-color: white; */
    display: flex;
    justify-content: center;
    align-items: center;
}
.profilepart-inner{
    width: 30px;
    height: 30px;
    background-color: transparent;
    border-radius: 50%;
    overflow: hidden;
}
.profilepart-inner img{
    width: 100%;
    height: auto;
}