.one,.two,.three,.four,.five,.six{
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin: 5px;
    background-color: rgba(255,255,255,0.5);
    /* background-color: rgba(0,0,0,0.5); */
}
.four,.five,.six{
    background-color: rgba(0,0,0,0.5);
}
.one{
    -webkit-animation: loader 0.5s ease-in-out 0s infinite normal forwards;
            animation: loader 0.5s ease-in-out 0s infinite normal forwards;
}
.two{
    -webkit-animation: loader 1s ease-in-out 0.25s infinite normal forwards;
            animation: loader 1s ease-in-out 0.25s infinite normal forwards;
}
.three{
    -webkit-animation: loader 1s ease-in-out 0.75s infinite normal forwards;
            animation: loader 1s ease-in-out 0.75s infinite normal forwards;
}
.four{
    -webkit-animation: loader1 0.5s ease-in-out 0s infinite normal forwards;
            animation: loader1 0.5s ease-in-out 0s infinite normal forwards;
}
.five{
    -webkit-animation: loader1 1s ease-in-out 0.25s infinite normal forwards;
            animation: loader1 1s ease-in-out 0.25s infinite normal forwards;
}
.six{
    -webkit-animation: loader1 1s ease-in-out 0.75s infinite normal forwards;
            animation: loader1 1s ease-in-out 0.75s infinite normal forwards;
}
@-webkit-keyframes loader {
    0%{
        background-color:rgba(255,255,255,0.5);
        /* background-color:rgba(0,0,0,0.5); */
    }
    50%{
        background-color: white;
        /* background-color: black; */
    }
    /* 60%{
        background-color: aqua;
    } */
    100%{
        background-color: rgba(255,255,255,0.5);
        /* background-color: rgba(0,0,0,0.5); */
    }
}
@keyframes loader {
    0%{
        background-color:rgba(255,255,255,0.5);
        /* background-color:rgba(0,0,0,0.5); */
    }
