.ripple{
    position: absolute;
    border-radius: 50%;
    -webkit-transform: scale(0);
            transform: scale(0);
    background-color: rgba(255,255,255,0.2);
    /* background-color: #90909050; */
    /* background-color: #50505080; */
}
.ripple.play{
    -webkit-animation: ripple 600ms linear;
            animation: ripple 600ms linear;
}
